import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import NotificationBar, { BAR_TYPES } from '@webui-one/components/Notification/NotificationBar';

const getMsg = (status, timestamp, rollbackId) => {
    const time = new Date(timestamp).toLocaleTimeString();

    if (status === 'started') {
        return `Commit started at ${time}`;
    }
    if (status === 'finished') {
        return `Commit finished successfully at ${time} with rollback id ${rollbackId}`;
    }
    if (status === 'failed') {
        return `Commit failed at ${time}`;
    }
    return null;
};

const displayTime = 5000;

const CommitStatusBar = ({
    commitCompleteTime,
    commitFailTime,
    commitInProgress,
    commitStartTime,
    rollbackId,
}) => {
    const [notificationMsg, setNotificationMsg] = useState(null);
    const [alarmMsg, setAlarmMsg] = useState(null);
    useEffect(() => {
        if (commitInProgress) {
            setNotificationMsg(getMsg('started', commitStartTime));
        } else if (commitFailTime) {
            setNotificationMsg(null);
            setAlarmMsg(getMsg('failed', commitFailTime));
            const timeoutId = setTimeout(() => {
                setAlarmMsg(null);
            }, displayTime);
            return () => clearTimeout(timeoutId);
        } else if (commitCompleteTime && Date.now() - commitCompleteTime <= displayTime) {
            setNotificationMsg(getMsg('finished', commitCompleteTime, rollbackId));
            const timeoutId = setTimeout(() => {
                setNotificationMsg(null);
            }, displayTime);
            return () => clearTimeout(timeoutId);
        } else {
            setNotificationMsg(null);
            setAlarmMsg(null);
        }
        return () => null;
    }, [commitInProgress, commitFailTime, commitCompleteTime, commitStartTime, rollbackId]);

    return (
        <>
            <NotificationBar message={notificationMsg} />
            <NotificationBar message={alarmMsg} type={BAR_TYPES.ALARM} />
        </>
    );
};

CommitStatusBar.propTypes = {
    commitInProgress: PropTypes.bool,
    commitStartTime: PropTypes.number,
    commitFailTime: PropTypes.number,
    commitCompleteTime: PropTypes.number,
    rollbackId: PropTypes.number,
};
export default CommitStatusBar;

