import { connect } from 'react-redux';
import {
    selectCommitInProgress,
    selectCommitFailTime,
    selectCommitStartTime,
    selectCommitCompleteTime,
    selectRollbackId,
} from 'modules/transactions/selectors';
import CommitStatusBar from './CommitStatusBar';

const mapStateToProps = state => ({
    commitCompleteTime: selectCommitCompleteTime(state),
    commitFailTime: selectCommitFailTime(state),
    commitInProgress: selectCommitInProgress(state),
    commitStartTime: selectCommitStartTime(state),
    rollbackId: selectRollbackId(state),
});

export default connect(
    mapStateToProps,
    null,
)(CommitStatusBar);
