import React from 'react';
import PropTypes from 'prop-types';
import Loader from '@webui-one/components/Loading/Loader';
import ModalConfirm from '@webui-one/components/Modal/ModalConfirm';
import style from './CommitDialogStyles';
import CommitOptionsContainer from './CommitOptionsContainer';
import IgnoreWarnings from './IgnoreWarnings';

const CommitDialog = ({
    commitInProgress,
    isOkToCommit,
    ignoreWarnings,
    onCancel,
    onCommit,
    onToggleIgnoreWarnings,
    validationStatus,
}) => (
    <ModalConfirm
        confirmTitle="Yes, commit"
        onConfirm={onCommit}
        onCancel={onCancel}
        formIsValid={isOkToCommit}
    >
        <div style={style.header}>Commit changes to NSO?</div>
        {commitInProgress || <CommitOptionsContainer />}
        {validationStatus && (
            <IgnoreWarnings
                ignoreWarnings={ignoreWarnings}
                onToggleIgnoreWarnings={onToggleIgnoreWarnings}
                validationStatus={validationStatus}
            />
        )}
        {commitInProgress && <Loader />}
    </ModalConfirm>
);

CommitDialog.propTypes = {
    commitInProgress: PropTypes.bool,
    ignoreWarnings: PropTypes.bool,
    isOkToCommit: PropTypes.bool,
    onCancel: PropTypes.func,
    onCommit: PropTypes.func.isRequired,
    onToggleIgnoreWarnings: PropTypes.func,
    validationStatus: PropTypes.string,
};

export default CommitDialog;
