import PropTypes from 'prop-types';
import React, { useCallback, useState } from 'react';
import EditSymbol from '@webui-one/styles/static/EditSymbol';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import style from './CommitDialogStyles';
import { COMMIT_OPTION_CONSTANTS, COMMIT_QUEUE_OPTION_CONSTANTS } from './CommitOptionConstants';
import CommitOption from './CommitOption';

const mapValues = (options, values) => options.map((option) => {
    const { id } = option;
    return {
        ...option,
        value: values[id],
    };
});

const renderOptions = (options, isConfigMode, onSetCommmitOption) => options.map(option => (
    <CommitOption
        key={option.id}
        isConfigMode={isConfigMode}
        onSetCommmitOption={onSetCommmitOption}
        option={option}
    />
));

const CommitOptions = ({ commitOptionValues, onSetCommmitOption }) => {
    const [isConfigMode, setIsConfigMode] = useState(false);

    const { 'commit-queue': commitQueue } = commitOptionValues;

    const commitOptions = mapValues(COMMIT_OPTION_CONSTANTS, commitOptionValues);

    const commitQueueOptions = mapValues(COMMIT_QUEUE_OPTION_CONSTANTS, commitOptionValues);

    const hasOption = commitOptions.some(({ value }) => value);

    const onClick = useCallback(() => setIsConfigMode(prev => !prev), []);

    return (
        <div style={style.frame}>
            <div style={style.topRow}>
                <div style={style.name}>
                    {(isConfigMode || hasOption)
                        ? 'Commit options:'
                        : 'No commit options set'
                    }
                </div>
                <button
                    type="button"
                    data-handle="penButton"
                    onClick={onClick}
                    style={style.editButton}
                >
                    <EditSymbol color={defaultStyles.color1} active={isConfigMode} />
                </button>
            </div>
            {(isConfigMode || hasOption) && (
                <div style={style.optionsBox}>
                    {renderOptions(commitOptions, isConfigMode, onSetCommmitOption)}
                    {(commitQueue && commitQueue !== 'bypass') && (
                        <div style={style.queueOptionsWrapper}>
                            {renderOptions(commitQueueOptions, isConfigMode, onSetCommmitOption)}
                        </div>
                  )}
                </div>
            )}
        </div>
    );
};

CommitOptions.defaultProps = {
    commitOptionValues: {},
    onSetCommmitOption: () => null,
};

export const CommitOptionsType = PropTypes.shape({
    'commit-queue-atomic': PropTypes.boolean,
    'commit-queue-block-others': PropTypes.boolean,
    'commit-queue': PropTypes.boolean,
    'commit-queue-error-option': PropTypes.string,
    'commit-queue-lock': PropTypes.boolean,
    'commit-queue-tag': PropTypes.string,
    'commit-queue-timeout': PropTypes.string,
    'no-deploy': PropTypes.boolean,
    'no-lsa': PropTypes.boolean,
    'no-networking': PropTypes.boolean,
    'no-out-of-sync-check': PropTypes.boolean,
    'no-overwrite': PropTypes.boolean,
    'no-revision-drop': PropTypes.boolean,
    'use-lsa': PropTypes.boolean,
});

CommitOptions.propTypes = {
    commitOptionValues: CommitOptionsType,
    onSetCommmitOption: PropTypes.func,
};


export default CommitOptions;
