import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { isNonEmptyArray } from '@webui-one/core/utils/array';
import { toNumber } from '@webui-one/core/utils/number';
import { isNonEmptyString } from '@webui-one/core/utils/string';
import { isDeepEqual, isNonEmpty } from '@webui-one/core/utils/object';
import { formatPath } from '@webui-one/core/utils/path';
import Loader from '@webui-one/components/Loading/Loader';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import {
    TAGS,
    WARNING_MULTIPLE_WEBUI_ONE,
} from '@webui-one/core/jsonrpc/constants';

import {
    getAdditionalTrans,
    getWriteTrans,
    getTransChanges,
    reevaluate,
    resolveConflicts,
    setActiveThAction,
} from 'modules/transactions/actions';
import {
    selectWriteTrans,
    selectConflicts,
    selectChangesWithDefaultValues,
    selectValidateTransactionError,
    selectReadWrite,
    selectReadWriteThTag,
    selectThIsBusy,
    selectStatus,
    selectWarnings,
    transactionIsEmpty,
} from 'modules/transactions/selectors';
import { selectSchema as _selectSchema } from 'modules/schemas/selectors';

import ChangesTabContainer from './ChangesTabContainer';
import CommitDryRunTabContainer from './CommitDryRunTabContainer';
import CommitManager, { pageProp } from './CommitManager';
import CommitQueueTab from './CommitQueueListContainer';
import NativeCommitDryRunTabContainer from './NativeCommitDryRunTabContainer';
import style from './CommitManagerStyles';
import WarningsTab from './WarningsTab';

const APP_NAME = 'CommitManager';
const PAGE_CHANGES = 'changes';
const PAGE_QUEUE = 'commit-queue';
const PAGE_ERROR = 'errors';
const PAGE_WARNINGS = 'warnings';
const PAGE_CONFIG = 'config';
const PAGE_NATIVE = 'native';

const DEFAULT_PAGE_ID = PAGE_CHANGES;

const PAGES = [
    PAGE_CHANGES,
    PAGE_WARNINGS,
    PAGE_ERROR,
    PAGE_CONFIG,
    PAGE_NATIVE,
    PAGE_QUEUE,
];

const LoaderBox = () => <div style={style.loaderbox}><Loader color={defaultStyles.color9} /></div>;

const getPage = ({ name, tab, page, id }, isDisabled) => ({
    isDisabled: !!isDisabled,
    id,
    name,
    page,
    tab: tab || name,
    to: `${formatPath(APP_NAME)}/${id}`,
});

const getChangesPage = () => ({
    id: PAGE_CHANGES,
    name: 'changes',
    page: <ChangesTabContainer />,
});

const getCommitDryRunPage = () => ({
    id: PAGE_CONFIG,
    name: 'config',
    page: <CommitDryRunTabContainer />,
});

const getNativeCommitDryRunPage = () => ({
    id: PAGE_NATIVE,
    name: 'native config',
    page: <NativeCommitDryRunTabContainer />,
});

const getCommitQueuePage = () => ({
    id: PAGE_QUEUE,
    name: 'commit queue',
    page: <CommitQueueTab />,
});

const countNumberOfErrors = (error) => {
    if (error && error.data && error.data.errors && Array.isArray(error.data.errors)) {
        return error.data.errors.length;
    }
    return 0;
};
const getTransName = ({ th, tag }) => {
    if (!th) {
        return '';
    }
    const thTag = tag ? ` - ${tag}` : '';
    return `${th}${thTag}`;
};

const convertWarningsToErrors = (warnings) => {
    if (isNonEmptyArray(warnings)) {
        return {
            data: {
                errors: warnings,
            },
        };
    }
    return undefined;
};

const getWarningsPage = (warnings, onReevaluateTrans) => {
    const numberOfWarnings = countNumberOfErrors(warnings);
    const name = 'warnings';
    return {
        id: PAGE_WARNINGS,
        tab: (
            <>
                <span>{name}</span>
                {numberOfWarnings > 0 && (
                    <span style={style.warningsNumberContainer} data-handle="warnings-number-label">
                        {numberOfWarnings}
                    </span>
                )}
            </>
        ),
        page: (
            <WarningsTab
                error={warnings}
                onRevalidateTrans={onReevaluateTrans}
            />
        ),
        name,
    };
};

const getErrorsPage = (error, conflicts, onReevaluateTrans, resolveConflictsCb, thTag) => {
    const numberOfErrors = countNumberOfErrors(error);
    const name = 'errors';
    return {
        id: PAGE_ERROR,
        tab: (
            <>
                <span>{name}</span>
                {numberOfErrors > 0 && (
                    <span style={style.warningsNumberContainer} data-handle="warnings-number-label">
                        {numberOfErrors}
                    </span>
                )}
            </>
        ),
        page: (
            <WarningsTab
                emptyMessage="No errors"
                error={error}
                conflicts={conflicts}
                onRevalidateTrans={onReevaluateTrans}
                onResolveConflicts={resolveConflictsCb}
                disableDataContainer={thTag !== TAGS.WEBUI_ONE}
            />
        ),
        name,
    };
};

export const CommitContent = ({ hasInitiated, thIsBusy, page }) => {
    if (!hasInitiated) return null;
    if (page.isDisabled) {
        return (
            <div style={style.message} data-handle="disabled-page">
                {`${page.name} is disabled`}
            </div>
        );
    }
    return (
        <>
            {thIsBusy && <LoaderBox />}
            {page.page}
        </>
    );
};

CommitContent.propTypes = {
    hasInitiated: PropTypes.bool,
    page: pageProp,
    thIsBusy: PropTypes.bool,
};

class CommitManagerContainer extends React.Component {
    constructor(props) {
        super(props);
        this.handleReevaluateTrans = this.handleReevaluateTrans.bind(this);
        this.resolveConflicts = this.resolveConflicts.bind(this);
        this.forceActivePage = this.forceActivePage.bind(this);
        this.reevaluateCalled = false;

        this.state = {
            redirected: false,
            hasInitiated: false,
        };
    }

    componentDidMount() {
        const {
            emptyTransaction,
            onGetWriteTrans,
            onGetAdditionalTrans,
            th,
            thIsBusy,
        } = this.props;

        if (!th) onGetWriteTrans();

        onGetAdditionalTrans();

        if (
            th
            && !thIsBusy
            && !emptyTransaction
        ) {
            this.handleReevaluateTrans(true);
        }
        this.setState({ hasInitiated: true });
    }

    shouldComponentUpdate(nextProps, nextState) {
        const {
            changes,
            currentPage,
            th,
            thList,
            thIsBusy,
            transStatus,
        } = this.props;
        const { hasInitiated } = this.state;
        if (hasInitiated !== nextState.hasInitiated) {
            return true;
        }
        if (currentPage !== nextProps.currentPage) {
            return true;
        }
        if (th !== nextProps.th) {
            this.reevaluateCalled = false;
            return true;
        }
        if (thList.length !== nextProps.thList.length) {
            return true;
        }
        if (thIsBusy === true && nextProps.thIsBusy === false) {
            return true;
        }
        if (transStatus !== nextProps.transStatus) {
            return true;
        }
        if (!isDeepEqual(changes, nextProps.changes)) {
            return true;
        }

        return false;
    }

    componentDidUpdate(prevProps) {
        const {
            emptyTransaction,
            onGetWriteTrans,
            th,
            thIsBusy,
            transError,
            warnings,
        } = this.props;

        if (th !== prevProps.th) {
            this.setRedirected(false);
        }

        if (!th && !emptyTransaction) {
            onGetWriteTrans();
        }

        if (th && !thIsBusy && !this.reevaluateCalled && !emptyTransaction) {
            this.handleReevaluateTrans();
        }

        if (!isDeepEqual(transError, prevProps.transError)) {
            this.forceActivePage('errors');
        } else if (!isDeepEqual(warnings, prevProps.warnings)) {
            this.forceActivePage('warnings');
        }
    }

    componentWillUnmount() {
        const { onSelectTh } = this.props;
        onSelectTh(undefined);
    }

    handleReevaluateTrans() {
        const { onReevaluate, th } = this.props;
        if (th) {
            this.reevaluateCalled = true;
            onReevaluate(th);
        }
    }

    setRedirected(redirected) {
        this.setState({ redirected });
    }

    forceActivePage(page) {
        const { currentPage, router } = this.props;
        const { redirected } = this.state;

        if (currentPage !== page && !redirected) {
            this.setRedirected(true);
            router.push(`${formatPath(APP_NAME)}/${page}`);
        }
    }
    resolveConflicts() {
        const { onResolveConflicts, th } = this.props;

        if (th) {
            onResolveConflicts(th);
        }
    }
    render() {
        const {
            conflicts,
            currentPage,
            emptyTransaction,
            onSelectTh,
            th,
            thIsBusy,
            thList,
            thTag,
            transError,
            transStatus,
            warningMessage,
            warnings,
        } = this.props;
        const { hasInitiated } = this.state;
        const disableDryRun = transError && isNonEmpty(transError);
        const pages = [
            getPage(getChangesPage()),
            getPage(getErrorsPage(
                transError,
                conflicts,
                this.handleReevaluateTrans,
                this.resolveConflicts,
                thTag,
            )),
            getPage(getWarningsPage(warnings, this.handleReevaluateTrans)),
            getPage(getCommitDryRunPage(), disableDryRun),
            getPage(getNativeCommitDryRunPage(), disableDryRun),
            getPage(getCommitQueuePage()),
        ];
        const properPageId = PAGES.includes(currentPage) ? currentPage : DEFAULT_PAGE_ID;
        const page = pages.find(({ id }) => properPageId === id);
        page.isActive = true;

        const selectedTh = getTransName({ th, tag: thTag });

        return (
            <CommitManager
                emptyTransaction={emptyTransaction}
                loading={thIsBusy}
                onChangeTab={this.onChangeTab}
                onSelectTh={onSelectTh}
                pages={pages}
                selectedTh={selectedTh}
                thList={thList}
                transStatus={transStatus}
                warningMessage={warningMessage}
            >
                <CommitContent thIsBusy={thIsBusy} page={page} hasInitiated={hasInitiated} />
            </CommitManager>
        );
    }
}

CommitManagerContainer.defaultProps = {
    emptyTransaction: undefined,
    thIsBusy: false,
    th: undefined,
    thList: [],
    transStatus: undefined,
    warnings: undefined,
};

CommitManagerContainer.propTypes = {
    changes: PropTypes.arrayOf(PropTypes.shape()),
    conflicts: PropTypes.arrayOf(PropTypes.shape()),
    currentPage: PropTypes.string,
    emptyTransaction: PropTypes.bool,
    onSelectTh: PropTypes.func.isRequired,
    onGetWriteTrans: PropTypes.func.isRequired,
    onGetAdditionalTrans: PropTypes.func.isRequired,
    onReevaluate: PropTypes.func.isRequired,
    onResolveConflicts: PropTypes.func.isRequired,
    router: PropTypes.shape({
        push: PropTypes.func,
        location: PropTypes.shape({}),
    }),
    thIsBusy: PropTypes.bool,
    th: PropTypes.number,
    thTag: PropTypes.string,
    thList: PropTypes.arrayOf(PropTypes.string),
    transError: PropTypes.shape({
        type: PropTypes.string,
        message: PropTypes.string,
    }),
    transStatus: PropTypes.string,
    warningMessage: PropTypes.string,
    warnings: PropTypes.shape({
        path: PropTypes.string,
        paths: PropTypes.arrayOf(PropTypes.string),
        reason: PropTypes.string,
    }),
};

const mapDispatchToProps = dispatch => ({
    onGetAdditionalTrans() {
        dispatch(getAdditionalTrans());
    },
    onGetWriteTrans() {
        dispatch(getWriteTrans());
    },
    onReevaluate(th) {
        dispatch(reevaluate({ th }));
    },
    onResolveConflicts(th) {
        dispatch(resolveConflicts({ th }));
    },
    onSelectTh(option) {
        let newTh;
        if (isNonEmptyString(option)) {
            const thStr = option.match(/\d/)[0];
            newTh = toNumber(thStr) || undefined;
        }
        dispatch(setActiveThAction(newTh));
        dispatch(getTransChanges({ th: newTh }));
    },
});

const mapStateToProps = (
    state,
    ownProps,
    {
        funcs: {
            selectSchema = _selectSchema,
        } = {},
    } = {},
) => {
    const routeParams = (ownProps && ownProps.routeParams) || {};
    const currentPage = (routeParams.splat || '').substr(1);
    const warnings = convertWarningsToErrors(selectWarnings(state));
    let transError = selectValidateTransactionError(state);

    if (transError && transError.data && Array.isArray(transError.data.errors)) {
        const errors = transError.data.errors.map(error => ({
            ...error,
            schema: selectSchema(
                state,
                error.path,
            ),
        }));
        transError = {
            ...transError,
            data: {
                ...transError.data,
                errors,
            },
        };
    }

    const trans = selectWriteTrans(state);
    const thList = Object.values(trans).map(getTransName);
    const hasMultiWriteTh = trans.filter(({ tag }) => tag === TAGS.WEBUI_ONE).length > 1;
    const warningMessage = hasMultiWriteTh
        ? WARNING_MULTIPLE_WEBUI_ONE
        : '';
    return {
        changes: selectChangesWithDefaultValues(state),
        conflicts: selectConflicts(state),
        currentPage,
        emptyTransaction: transactionIsEmpty(state),
        th: selectReadWrite(state),
        thIsBusy: selectThIsBusy(state),
        thList,
        thTag: selectReadWriteThTag(state),
        transError,
        transStatus: selectStatus(state),
        warningMessage,
        warnings,
    };
};

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(CommitManagerContainer);

export {
    CommitManagerContainer as _CommitManagerContainer,
    convertWarningsToErrors as _convertWarningsToErrors,
    countNumberOfErrors as _countrNumberOfErrors,
    getChangesPage as _getChangesPage,
    getCommitDryRunPage as _getCommitDryRunPage,
    getNativeCommitDryRunPage as _getNativeCommitDryRunPage,
    getWarningsPage as _getWarningsPage,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
