import React from 'react';
import PropTypes from 'prop-types';
import { Link } from '@webui-one/router';

import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import Select from '@webui-one/components/ConfigRender/Select/Select';
import CommitButtonContainer from 'Common/Components/Commit/CommitButtonContainer';
import RevertButtonContainer from 'Common/Components/Commit/RevertButtonContainer';
import LoadButtonContainer from 'Common/Components/Commit/LoadButtonContainer';
import HeaderContainer from 'Common/Components/Header/HeaderContainer';
import FooterContainer from 'Common/Components/Footer/FooterContainer';
import EditModeButtonContainer from 'ConfigurationEditor/EditModeButtonContainer';
import style, { tabs as tabStyle } from './CommitManagerStyles';
import { transactionStatus } from 'modules/transactions/constants';
import CommitStatusBarContainer from 'Common/Components/Commit/CommitStatusBarContainer';
import NotificationBar, { BAR_TYPES } from '@webui-one/components/Notification/NotificationBar';

const getStatusElement = (transactionEmpty, transStatus, loading) => {
    if (loading) {
        return (
            <span data-handle="transactionStatus" style={style.statusStyles.statusValid}>
                EVALUATING
            </span>
        );
    }

    if (transactionEmpty) {
        return (
            <span data-handle="transactionStatus" style={style.statusStyles.statusEmpty}>
                EMPTY
            </span>
        );
    }
    if (transStatus === transactionStatus.INVALID) {
        return (
            <span data-handle="transactionStatus" style={style.statusStyles.statusInvalid}>
                INVALID
            </span>
        );
    }
    return (
        <span data-handle="transactionStatus" style={style.statusStyles.statusValid}>
            VALID
        </span>
    );
};

const getTabStyle = (page) => {
    if (page.isDisabled) {
        return tabStyle.disabledtab;
    }
    if (page.isActive) {
        return tabStyle.activeTab;
    }
    return tabStyle.tab;
};

const TabLink = ({ page }) => {
    const isLink = !page.isDisabled && !page.isActive;
    const LinkComponent = isLink ? Link : 'span';
    return (
        <div data-handle="tab-btn" style={getTabStyle(page)}>
            <LinkComponent
                to={page.to}
                style={tabStyle.tabButton}
            >
                {page.tab}
            </LinkComponent>
        </div>
    );
};

const SelectTh = ({
    thList,
    onSelectTh,
    selectedTh,
}) => (
    <div style={style.selectTh}>
        {(thList.length > 1 || (thList.length > 0 && !thList.includes(selectedTh))) && (
            <Select
                editable
                name={'select-th'}
                extraSelectStyle={style.activeThSelect}
                onSetValue={onSelectTh}
                options={thList}
                placeholder="Select active transaction handle"
                value={selectedTh}
            />
        )}
    </div>
);

SelectTh.propTypes = {
    onSelectTh: PropTypes.func,
    selectedTh: PropTypes.string,
    thList: PropTypes.arrayOf(
        PropTypes.string,
    ),
};
export const pageProp = PropTypes.shape({
    isActive: PropTypes.bool,
    isDisabled: PropTypes.bool,
    name: PropTypes.string,
    tab: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.object,
    ]),
    to: PropTypes.string,
});

TabLink.propTypes = ({
    page: pageProp,
});
const additionalComponentsRight = [<EditModeButtonContainer />];

const CommitManager = ({
    children,
    emptyTransaction,
    loading,
    onSelectTh,
    pages,
    selectedTh,
    thList,
    transStatus,
    warningMessage,
}) => (
    <div style={style.application} data-handle="commitManager">
        <HeaderContainer additionalComponentsRight={additionalComponentsRight}>
            <div style={style.secondHeader}>
                <div style={style.contentWrapper}>
                    <div style={style.statusStyles.message}>
                        <span>
                            Current transaction
                            {thList.length === 1 && selectedTh && (
                            <span title={`Active transaction ${selectedTh}`}>
                                {` (${selectedTh}) `}
                            </span>
                            )}
                            {' '}
                            is
                        </span>
                            &#160;
                        {getStatusElement(emptyTransaction, transStatus, loading)}
                    </div>
                    <SelectTh
                        onSelectTh={onSelectTh}
                        selectedTh={selectedTh}
                        thList={thList}
                    />
                    <RevertButtonContainer />
                    <LoadButtonContainer />
                    <div style={style.spacer} />
                    <div style={style.commitButton}>
                        <CommitButtonContainer>
                            Commit
                        </CommitButtonContainer>
                    </div>
                </div>
            </div>
            <div style={defaultStyles.shadow} />
            <CommitStatusBarContainer />
            <NotificationBar
                message={warningMessage}
                type={BAR_TYPES.WARNING}
            />
        </HeaderContainer>
        <div style={style.scrollingContainer}>
            <div style={style.maxWidth}>
                <div style={tabStyle.main}>
                    <div style={tabStyle.tabContainer}>
                        { pages.map((page, index) => (
                            <TabLink key={index} page={page} />
                        ))}
                    </div>
                    {children}
                </div>
            </div>
        </div>
        <FooterContainer />
    </div>
);


CommitManager.propTypes = {
    children: PropTypes.node,
    emptyTransaction: PropTypes.bool,
    loading: PropTypes.bool,
    onSelectTh: PropTypes.func,
    pages: PropTypes.arrayOf(
        pageProp,
    ),
    selectedTh: PropTypes.string,
    transStatus: PropTypes.string,
    thList: PropTypes.arrayOf(
        PropTypes.string,
    ),
    warningMessage: PropTypes.string,
};

CommitManager.defaultProps = {
    pages: [],
    loading: undefined,
    selectedTh: '',
};

export default CommitManager;

export {
    getStatusElement as _getStatusElement,
};
