import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import style from './CommitDialogStyles';
import CheckBox from '@webui-one/components/ConfigRender/CheckBox/CheckBox';
import Input from '@webui-one/components/ConfigRender/Input/Input';
import Select from '@webui-one/components/ConfigRender/Select/Select';

const renderName = (isConfigMode, name, value) => (isConfigMode ? `${name}` : `${name}: ${value}`);

const CommitOption = ({
    isConfigMode,
    onSetCommmitOption,
    option,
}) => {
    const {
        formatOption,
        id,
        name,
        selectOptions,
        selectOptionsInfo,
        type,
        value,
    } = option;

    const onSetValue = useCallback(v => onSetCommmitOption(id, v), [id, onSetCommmitOption]);

    if (isConfigMode || value) {
        if (type === 'checkbox') {
            return (
                <div data-handle={id} style={style.row}>
                    <div style={style.name}>{name}</div>
                    {isConfigMode && (
                        <CheckBox
                            value={!!value}
                            onSetValue={onSetValue}
                        />
                    )}
                </div>
            );
        }
        if (type === 'select') {
            return (
                <div data-handle={id} style={style.row}>
                    <div style={style.name}>{renderName(isConfigMode, name, value)}</div>
                    {isConfigMode && (
                        <Select
                            options={selectOptions}
                            optionsInfo={selectOptionsInfo}
                            formatOption={formatOption}
                            onSetValue={onSetValue}
                            value={value}
                        />
                    )}
                </div>
            );
        }
        if (type === 'input') {
            return (
                <div data-handle={id} style={style.row} onKeyDown={e => e.stopPropagation()} role="none">
                    <div style={style.name}>{renderName(isConfigMode, name, value)}</div>
                    <div style={style.inputWrapper}>
                        {isConfigMode && (
                            <Input
                                editable
                                onSetValue={onSetValue}
                                value={value}
                            />
                        )}
                    </div>
                </div>
            );
        }
    }
    return null;
};

CommitOption.propTypes = {
    isConfigMode: PropTypes.bool,
    onSetCommmitOption: PropTypes.func,
    option: PropTypes.shape({
        formatOption: PropTypes.func,
        id: PropTypes.string,
        name: PropTypes.string,
        selectOptions: PropTypes.arrayOf(PropTypes.string),
        selectOptionsInfo: PropTypes.arrayOf(PropTypes.string),
        type: PropTypes.string,
        value: PropTypes.oneOfType([
            PropTypes.bool,
            PropTypes.string,
        ]),
    }),
};

export default CommitOption;
